@import '_variables';
@import '_mixins';
@import '~video.js/dist/video-js.css';
@import '~videojs-dock/dist/videojs-dock.css';

$assets-path: '../../assets/';
@import '../../sass/player/index';

.standalone-video-embed {
  margin: 0 !important;
  background-color: black;
}


.video-js-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  width: 100%;
}

.vertcalVideoContainer {
  height: 100%;
  width: auto;
}


video {
  width: 100%;
}

video[src], .vjs-fullscreen {
  padding: 0 !important;
}

#video-wrapper {
  width: 100%;
  height: 100%;
}

.video-js{
  background: transparent;
  font-family: 'Segoe UI', SegoeUI, "Helvetica Neue", Helvetica, Arial, sans-serif!important;

  .vjs-control-bar{
    background: rgba(1, 18, 37, 0.75);
  }
}



.video-js .vjs-tech {
  position: absolute;
  left : 0;
  top : 0;
  bottom : 0;
  right: 0;
}

.standalone-video-embed {
  .video-js .vjs-tech {
    position: relative;
    height: auto;
  }

  .verticalVideo {
    height: 100%;
    
    .vjs-tech {
      height: 100%;
    }
  }
}

.video-js:not(.verticalVideo) {
  height: auto !important;
}

.video-js {
  position: relative;

  

  &.vjs-fullscreen {
    video {
      max-height: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.vjs-peertube {
  display: none !important;
}

.video-js {
  width: 100%;
  overflow: hidden;
  height: auto!important;

  .vjs-poster {
    background-size: 100% auto;
    left: -1px;
    top : -1px;
    bottom : -1px;
    right: -1px;
    height: auto;
  }

  @media screen and (max-width: 350px) {
    .vjs-play-control {
      padding: 0 5px !important;
      width: 25px !important;
    }

    .vjs-volume-control {
      display: none !important;
    }

    .vjs-volume-panel {
      width: 26px !important;
    }

    .vjs-peertube-link {
      padding: 0;
    }
  }
}
